import React, {Component} from 'react';
import { Card,Dropdown, DropdownButton, Spinner } from 'react-bootstrap';


class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
          show: true,
          categories: []
        };
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
        this.handleCategoryChange = this.handleCategoryChange.bind(this);

      }

    componentDidMount() {
        // Get categories
        this.getCategories();
    }

    showModal = () => {
        this.setState({ show: true });
    };

    hideModal = () => {
        this.setState({ show: false });
    };

    handleCategoryChange = (e) => {
        this.props.handleCategoryChange(e)
    };

    getCategories = async () => {
        // Category lookup https://opentdb.com/api_category.php
        fetch("https://opentdb.com/api_category.php")
        .then(res => res.json())
        .then(
        (res) => {
            this.setState({
                categories: res.trivia_categories,
                isLoaded: true
            }) 
        });

      
    }

    render() {
        const { isLoaded } = this.state;

        if (!isLoaded) {
            return (
            <>
            <h3>Loading</h3>
            <h5><Spinner animation="grow" variant="info" size="sm"/>   <Spinner animation="grow" variant="info" size="sm"/>   <Spinner animation="grow" variant="info" size="sm"/></h5>
              </>
            )
        } else {
            return (
            <form onSubmit={this.hideModal}>
            <Card show={this.state.show}>
                <Card.Header>
                <Card.Title>Trivia</Card.Title>
                </Card.Header>
                <Card.Body>
                <Dropdown >
                    <Dropdown.Header>Category</Dropdown.Header>
                    <DropdownButton 
                        variant="success" 
                        id="dropdown-basic"
                        title={this.props.category}
                        onSelect={this.handleCategoryChange}
                    >
                        {this.state.categories.map((cat, index) => {
                            <Dropdown.Item eventKey={cat.id}>{cat.name}</Dropdown.Item>
                        })}
                    </DropdownButton>
                </Dropdown>
                <p>Press Start to Play</p>
                </Card.Body>
                <Card.Footer>
                    <button onClick={this.props.startGame}>Start</button>
                </Card.Footer>
            </Card>
            </form>
        );
        }
    }
}
   
export default Menu;